import React from 'react';

const AgenciasViaje = () => {
    return (
        <>
            <section className="block__B container">
                <article className="grid grid--2-cols feature">
                    <div>
                        <h3 className="header card__A">Agencias de viaje </h3>
                        <p className="feature__text">
                        Las Agencias de Viaje son las Entidades Tursticas de Mayor tradicin en el Sector Turismo, por lo regular son pequeas o medianas empresas PYME legalmente constituidas que como fuente de negocio promueven Destinos Tursticos Nacionales e Internacionales.
                        <br/> <br/> 
                        Una Agencia de Viaje tiene la capacidad de ofrecer a mercado abierto Boletos de Avin, Cuartos de Hotel, Paquetes y Traslados entre otros productos, estas Agencias se caracterizan por ofrecer medios de pago variados como Pagos con Tarjeta de Crdito, Depsitos Bancarios e inclusive pagos con Efectivo si es necesario, esta Facilidad de Pago le permite a los clientes pagar sus Viajes a Meses SIN Intereses con Tarjetas de Crdito Bancarias.
                        <br/> <br/>
                        Una Agencia de Viaje legalmente constituida debe darse de alta como cualquier otra empresa que desee emitir facturas y contar con un historial fiscal. En Mxico existen diversas cmaras y asociaciones que promueven el crecimiento de las Agencias de Viaje, algunas y Asociaciones Tursticas reconocidas en el pas son las siguientes:
                        <br/> <br/>
                        </p>
                        <ul>
                            <li className="feature__text">
                            Asociacin Nacional de Agencias de Viaje (ANAV)
                            </li>
                            <li className="feature__text">
                            Consejo Nacional Empresarial Turstico (CNET)
                            </li>
                            <li className="feature__text">
                            Abastecedores Tursticos (AMAIT)
                            </li>
                            <li className="feature__text">
                            Asociacin Mexicana de Desarrolladores Tursticos (AMDETUR)
                            </li>
                            <li className="feature__text">
                            Asociacin Mexicana de Hoteles y Moteles (AMHM)
                            </li>
                            <li className="feature__text">
                            Confederacin Nacional de Asociaciones de Agencias de Viajes de Mxico (CONAAV)
                            </li>
                            <li className="feature__text">
                            Confederacin de Cmaras Nacionales de Comercio, Servicios y Turismo (CONCANACO-SERVYTUR)
                            </li>
                            <li className="feature__text">
                            Confederacin Nacional Turstica (A.C.,CNT)
                            </li>
                        </ul>
                        <p className="feature__text">
                        <br/> 
                        En adicin a las cmaras es posible ubicar a cientos de Agencias de Viaje que operan de forma fsica en el pas, estas agencias son puntos de venta tursticos y muchas de ellas son muy importantes en el desarrollo Turstico Nacional, algunas agencias de renombre son:
                        </p>
                    </div>
                    <picture className="grid__image">
                        <source
                        type="image/png"
                         srcset="https://i.postimg.cc/HxxtWKkZ/sprite5.png" />
                         <img className="media__image" src="https://i.postimg.cc/HxxtWKkZ/sprite5.png" alt="easy info" />
                    </picture>
                </article>            
                </section>
            
               
                <section className="grid__brandsSection container">
                    <div className="grid__brands">                        
                        <div className="grid__brandsBox">
                            <img src="https://i.postimg.cc/mgKJ07QZ/auto-Travel.jpg" alt="easy info" />
                        </div>
                        <div className="grid__brandsBox">
                            <img src="https://i.postimg.cc/hvKH2gV9/blueSky.jpg" alt="easy info" />
                        </div>
                        <div className="grid__brandsBox">
                            <img src="https://i.postimg.cc/4ywjMM1j/bojorque-Viajes.jpg" alt="easy info" />
                        </div>
                        <div className="grid__brandsBox">
                            <img src="https://i.postimg.cc/kMcrB6dw/ibero-Mexico.jpg" alt="easy info" />
                        </div>
                        <div className="grid__brandsBox">
                            <img src="https://i.postimg.cc/Hssq5YYm/mega-Travel.jpg" alt="easy info" />
                        </div>
                        <div className="grid__brandsBox">
                            <img src="https://i.postimg.cc/wTfSQCMm/tornare-Travel.jpg" alt="easy info" />
                        </div>
                        <div className="grid__brandsBox">
                            <img src="https://i.postimg.cc/2S1gc0LX/viajes-Fama.jpg" alt="easy info" />
                        </div>
                        <div className="grid__brandsBox">
                            <img src="https://i.postimg.cc/bvbW8dTx/viajes-Real.jpg" alt="easy info" />
                        </div>
                        <div className="grid__brandsBox">
                            <img src="https://i.postimg.cc/bJhMv8CK/viajes-Turquesa.jpg" alt="easy info" />
                        </div>
                    </div>        
                </section>
        </>
    );
};

export default AgenciasViaje;