import React from "react";

const Global = () => {
    return(
        <>
            <section className="block__B container">
                <article className="grid grid--2-cols feature">
                    <div>
                        <h3 className="header card__A">Globalizadores </h3>
                        <p className="feature__text">
                            Los Globalizadores o GDS (Global Distribution System) por sus siglas en ingls son los sistemas de comercializacin por excelencia de Boletos de Avin, estos sistemas concentran la oferta de Destinos Tursticos de casi todas las Aerolneas del planeta.<br/><br/>
                            La funcin principal de los Globalizadores o GDS es centralizar la operativa de Aerolneas Nacionales e Internacionales en un solo sistema; la idea es poner a disposicin de millones de usuarios la oferta de Boletos de Avin y Destinos Tursticos de forma sencilla y ordenada para que el usuario sea capaz de:
                        </p>
                        <ul>
                            <li className="feature__text">
                               Comprar Boletos de Avin de una o varias aerolneas para Viajar en Vuelos Directos, Vuelos Multi Destino o Vuelos Multi Aerolnea
                            </li>
                            <li className="feature__text">
                               Adquirir Boletos de Avin para Vuelos Nacionales o Vuelos Internacionales
                            </li>
                            <li className="feature__text">
                               Seleccionar los Boletos de Avin con Tarifas mas Bajas o con horarios de su conveniencia
                            </li>
                            <li className="feature__text">
                               Seleccionar la Aerolnea de su preferencia
                            </li>
                            <li className="feature__text">
                               Adquirir Boletos de Avin para cualquier fecha del ao
                            </li>
                        </ul>
                    </div>
                    <picture className="grid__image">
                        <source
                        type="image/png"
                         srcset="https://i.postimg.cc/Dz7gHmkK/sprite.png" />
                         <img className="media__image" src="https://i.postimg.cc/Dz7gHmkK/sprite.png" alt="easy info" />
                    </picture>
                </article>            
                </section>
            
               
                <section className="grid__brandsSection container">
                    <div className="grid__brands">
                        <div className="grid__header header__brands">Los globalizadores ms importantes del planeta son:</div>
                        <div className="grid__brandsBox">
                            <img src="https://i.postimg.cc/XvzbQrXs/amadeus.jpg" alt="easy info" />
                        </div>
                        <div className="grid__brandsBox">
                            <img src="https://i.postimg.cc/0ySD8S1w/sabre.jpg" alt="easy info" />
                        </div>
                        <div className="grid__brandsBox">
                            <img src="https://i.postimg.cc/q7dng7pZ/travelport.jpg" alt="easy info" />
                        </div>
                    </div>        
                </section>
        </>
    );
};

export default Global;